@import url(https://fonts.googleapis.com/css?family=Sofia);
html{
    overflow: hidden;
}

body {
    margin: 0;
    padding: 0;
    overflow: hidden;
    background: #202124;  
    /*background: -webkit-linear-gradient(to right, #8f94fb 100%, #4e54c8 100%); 
    background: linear-gradient(to top right, #615cce, #8f94fb 40%); */
}

a {
    color: #1976d2;
}

#main{
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    width: calc(100vw - 30px);
    height: calc(100vh - 30px);
    z-index: 1;
}

#main-content{
    height: 100%;
    width: 100%;
    opacity: 1;
    overflow-y: scroll;
    color: #c5c5c5;
    font-family: "Roboto", Arbutus Slab;
    background: transparent;
    z-index: -1;
    line-height: 1.5;
}

.top{
    height: 30%;
    margin: 0 60px;
    display: flex;
    border-bottom: 1px solid #c5c5c5;
    align-items: center;
    justify-content: center;
}

.top>.greetings{
   text-align: center;
   font-size: 4em;
   font-family: "Roboto", Sofia;
}

.about-me{
    /*height: 70%;*/
    max-height: 100%;
    font-size: 1.5em;
    text-align: justify;
    padding: 20px 60px;
}

.quote{
    display: block;
    text-align: center;
    font-style: italic;
    margin: 20px 0;
    color: #aaa;
}

#main-content::-webkit-scrollbar {
    width: 4px;
    opacity: 0.5;             
}
#main-content::-webkit-scrollbar-track {
    opacity: 0.5;   
}
#main-content::-webkit-scrollbar-thumb {
  border-radius: 20px; 
  border: 3px solid cornflowerblue; 
}

@media only screen and (max-width: 480px){
    .about-me{
        font-size: 1em;
        padding: 20px 30px;
    }
    .top>.greetings{
        font-size: 2em;
    }
    .top{
        margin: 0 30px;
    }
}
.right-tab{
    display: flex;
    height: 100vh;
}

.right-tab-arrow{
    width: 30px;
    background: #fff;
    z-index: 99;
    display: flex;
    justify-content: center;
    align-items: center;
}

.right-tab-content{
    width: 50vw;
    z-index: 90;
    background: #232526;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to top, #414345, #232526); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

@media only screen and (max-width: 480px){
    .right-tab-content{
        width: calc(100vw - 30px);
    }
}
#container{
    color: white;
    overflow-y: scroll;
    overflow-x: hidden;
    height: calc(100vh - 30px);
}

#heading{
    margin: 22px 0;
    font-family: Arbutus Slab;
    font-size: 2em;
    color: #DDFC74;
    text-align: center;
}
.project-item{
    font-family: Arbutus Slab;
    margin: 19px;
    border: 1px solid white;
    border-radius: 6px;
    display: flex;
    padding: 5px;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
    height: auto;
    height: initial;
}

.preview{
    width: 30%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    max-width: 100px;
    max-height: 100px;
    /*height: 75px;*/
}

.preview>img{
    width: 100%;
    height: 100%;
    border-radius: 10px;
}

.content{
    display: flex;
    flex-direction: column;
    width: 70%;
    text-align: left;
    padding: 5px;
    padding-left: 10px;
}

.title{
    font-size: 1.7em;
}

.description{
    font-size: 1em;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}

.go-to-website{
    align-self: flex-end;
    color: darkturquoise;
    text-decoration:none;
}

@media only screen and (max-width: 480px){
    .title{
        font-size: 1.5em;
    }
    .description{
        font-size: 0.9em;
    }
}
.bottom-tab{
    display: flex;
    flex-direction: column;
    width: 100vw;
}

.bottom-tab-arrow{
    position: absolute;
    top:0;
    height: 30px;
    width: 100vw;
    background: #fff;
    z-index: 100;
    display: flex;
    align-items: center;
    justify-content: center;
}

.bottom-tab-content{
    position: absolute;
    top: 30px;
    height: 50vh;
    width: 100%;
    z-index: 100;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    background: #000000;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to bottom, #434343, #000000); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

@media only screen and (max-width: 480px){
    .bottom-tab-content{
        height: calc(100vh - 30px);
    }
}
.container{
    display: flex;
    justify-content: space-evenly;
    width: 100%;
    height: 100%;
    align-items: center;
}

.main{
    text-align: left;
}

.heading{
    margin: 22px 0;
    font-family: Arbutus Slab;
    font-size: 2em;
    color: #DDFC74;
}

.sub-heading{
    font-size: 0.8em;
    text-align: left;
    color: grey;
    max-width: 300px;
}

#content{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 20px;
    gap: 20px;
}



@media only screen and (max-width: 480px){
    .heading{
        margin: 0;
    }
    .container{
        flex-direction: column;
        align-items: center;
    }
    .main{
        text-align: center;
    }
    .sub-heading{
        text-align: center;
    }
}
.linkedin{
    color: #2867B2;
}

.github{
    color: white;
}

.instagram{
    color: #ff8400;
}

.gmail{
    color: #b23121;
}
